.sidebar{
    top:80px;
    position:sticky;
    flex:0.2;
    border-radius:10px;
    text-align: center;
    height:fit-content;
    width:340px;
}

.sidebar__avatar{
    margin-bottom:10px;
}
.sidebar__top{
    display:flex;
    flex-direction: column;
    align-items: center;
    border:1px solid lightgray;
    border-bottom:0;
    border-top-left-radius:10px;
    border-top-right-radius:10px;
    background-color:white;
    padding-bottom:10px;
}
.sidebar__top > h4{
    color:gray;
    font-size:11px;
}
.sidebar__top > h2{
    font-size:18px;
}
.sidebar__top > img {
    margin-bottom:-20px;
    width:100%;
    height:60px;
    object-fit: cover;
    border-top-left-radius:10px;
    border-top-right-radius:10px;
}
.sidebar__stats{
    padding:10px;
    margin-bottom:10px;
    border:1px solid lightgray;
    background-color:white;
    border-bottom-left-radius:10px;
    border-bottom-right-radius:10px;

}

.sidebar__stat{
    margin-top: 10px;
    display:flex;
    justify-content: space-between;
}
.sidebar__stat > p {
    color:gray;
    font-size:13px;
    font-weight: 600;
}
.sidebar__statNumber{
    font-weight:bold;
    color:#0a66c2 !important;
}
.sidebar__top > h2{
    font-size:18px;
}

.sidebar__bottom{
    text-align: left;
    padding:10px;
    border: 1px solid lightgray;
    background-color:white;
    border-radius: 10px;
    margin-top: 10px;
}

.sidebar__recentItems{
    display:flex;
    font-size:13px;
    color:gray;
    font-weight:bolder;
    cursor:pointer;
    padding:5px;
    margin-bottom:5px;
}
.sidebar__recentItems:hover{
    background-color:whitesmoke;
    border-radius:10px;
    cursor:pointer;
    color:black;
}

.sidebar__hash{
    margin-right:10px;
    margin-left:10px;
}

.sidebar__bottom > p{
    font-size:13px;
    padding-bottom:10px;
}